<template>
  <div class="voucher-page">
    <Header class="header" :leftArrow="!$route.query.browerFirstIn">
      <!-- <div class="back" slot="left"></div> -->
      <div class="title" slot="title"><img src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/voucher/title.png"></div>
      <div class="share" slot="right" v-if="isUplus" @click="doShare"></div>
    </Header>
    <div class="voucher-nav-fixed">
      <div class="nav-warp-bg">
        <div class="nav-wap">
          <div :id="`cateid-${item.id}`" :class="{'active': couponCateId == item.id}" v-for="(item,index) in couponCateList" :key="index" @click="changeCateTab(item.id)">{{item.cateName}}</div>
        </div>
      </div>
      <div class="more" @click="toShowMore"></div>
    </div>
    <div class="mask" v-show="showMore" @click="showMore = false">
      <div class="nav-more">
        <span :class="{'active': couponCateId == item.id}" v-for="(item,index) in couponCateList" :key="index" @click="changeCateTab(item.id)">{{item.cateName}}</span>
      </div>
    </div>
    <div class="voucher-warp">
      <div class="warp-bg"></div>
      <div class="voucher-list">
        <van-list v-model="fetching" :finished="finished" finished-text="没有更多了" @load="onLoad">
          <div class="voucher-item" v-for="item in couponsCenterList" :key="item.id">
            <div class="voucher-body">
              <Tag :from="item.couponFrom" :type="item.couponType"/>
              <div class="voucher-prods">
                <div>
                  <div><img src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/voucher/imgnone.png"></div>
                  <p><small>￥</small>1399.00</p>
                </div>
                <div>
                  <div><img src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/voucher/quan.png"></div>
                  <p><small>￥</small>1399.00</p>
                </div>
                <div>
                  <div><img src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/voucher/quan.png"></div>
                  <p><small>￥</small>1399.00</p>
                </div>
              </div>
            </div>
            <div class="voucher-info" :class="{'voucher-disable': true}">
              <h5><small>￥</small>10000</h5>
              <p>满8000000元可用</p>
              <a href="javascript:;">{{true ? '去使用' : '点击领取'}}</a>
            </div>
          </div>
        </van-list>
      </div>
    </div>
  </div>
</template>

<script>
import Header from '@/components/header'
import Tag from './components/quan-tag'
import { mapGetters } from 'vuex'
import { getViewCouponCate, getCouponsCenter } from '@/http/service/shop'

export default {
  name: 'newvoucher',
  components: {
    Header,
    Tag,
  },
  data() {
    return {
      isUplus: this.$util.isHaierApp(),
      couponCateList: [], // 分类列表
      couponsCenterList: [], // 券列表
      couponCateId: '', // 当前分类id
      startIndex: 1,
      pageSize: 10,

      fetching: false,
      finished: false,
      showMore: false,
    }
  },
  computed: {
    ...mapGetters(['mId']),
    ...mapGetters({
      location: 'getlocationInfo',
    }),
  },
  created() {
    this.getViewCouponCate()
    // 二次分享
    this.$store.dispatch('WeChatSecondShare', {
      title: '智家商城领券频道，优惠券等您来抢',
      desc: '给您推荐智家商城领券频道，超值优惠券每日限量抢，不可错过哦！',
      thumImage: '',
    })
  },
  methods: {
    onLoad() {
      this.getCouponsCenter()
    },
    /**
     *
     */
    changeCateTab(id) {
      document.getElementById(`cateid-${id}`).scrollIntoView({ behavior: 'smooth', inline: 'center', block: 'nearest' })
      this.couponCateId = id
      this.couponsCenterList = []
      this.startIndex = 1
      this.finished = false
      this.getCouponsCenter()
    },
    /**
     * @description 领券中心 获取优惠券列表
     */
    getCouponsCenter() {
      getCouponsCenter({
        couponCateId: this.couponCateId,
        streetId: this.location.townshipId,
        pageSize: this.pageSize,
        startIndex: this.startIndex,
      }).then(res => {
        if (res.success) {
          this.couponsCenterList = this.couponsCenterList.concat(res.data || []);
          this.startIndex += 1;
          this.finished = res.data.length !== this.pageSize
          // console.log(this.finished, '=======')
        } else {
          this.$toast(res.message)
        }
      })
    },
    /**
     * @description 领券中心商品类目列表
     */
    getViewCouponCate() {
      getViewCouponCate().then(res => {
        if (res.success) {
          this.couponCateList = res.data;
          this.couponCateId = res.data[0].id;
        } else {
          this.$toast(res.message)
        }
      })
    },
    doShare() {
      this.$util.inAppShare({
        title: '智家商城领券频道，优惠券等您来抢',
        url: location.href.indexOf('?') !== -1 ? location.href + '&shareId=' + this.mId : location.href + '?shareId=' + this.mId,
        desc: '给您推荐智家商城领券频道，超值优惠券每日限量抢，不可错过哦！'
      })
    },
    toShowMore() {
      this.showMore = true
    }
  }
}
</script>

